
#Gantt-root {
  width: 100%;
  height: 100%;
  overflow: auto;
}
#Gantt-scale {  
  position: relative;
  top: 0;
  background-color: white;
  float: left;
}
#Gantt-scale-group text {
  font-size: 12px;
  fill: SteelBlue;
  font-weight: bold;
}
#Gantt-scale-group line {
  stroke-width: 0.7; 
  stroke: black;
}
#Gantt-bg-group line {
  stroke-width: 1.5;   
  stroke: lightgrey;
  stroke-opacity: 0.8;
  shape-rendering: crispEdges;
}
#Gantt-task-group rect {
  fill: SteelBlue;
  stroke-width: 0.5;
  stroke: black;
}
#Gantt-task-group text {
  fill: white;
  stroke: grey;
  font-weight: bold;
  font-size: 13px;
  /*font-family: impact;*/
  transform: translateY(-2px);
}



