
@import '~nouislider/distribute/nouislider.min.css';
.noUi-horizontal .noUi-handle {
  width: 12px;
  height: 20px;
  left: -6px;
  top: -2px;
}
.noUi-handle:after, .noUi-handle:before {
  left: 3px;
  top: 2px;
}
.noUi-handle:before {
  left: 3px;
}
.noUi-handle:after {
  left: 6px;
}



